@font-face {
  font-family: "Oswald";
  font-style: normal;
  font-weight: 400;
  font-display: swap;
  src: url("../fonts/oswaldregular.woff2") format("woff2"), url("../fonts/oswaldregular.woff") format("woff");
}
@font-face {
  font-family: "Oswald";
  font-style: normal;
  font-weight: 500;
  font-display: swap;
  src: url("../fonts/oswaldmedium.woff2") format("woff2"), url("../fonts/oswaldmedium.woff") format("woff");
}
:root {
  --accent-font: "Oswald", "Impact", sans-serif;
  --main-font: "Arial", "Helvetica", sans-serif;
}

.visually-hidden {
  position: absolute;
  transform: scale(0);
}

.page {
  font-family: var(--main-font);
}

.page__body {
  width: 100%;
  font-family: var(--accent-font);
  font-weight: 400;
}

.page__footer {
  background-color: #f2f2f2;
}

.button {
  display: block;
  padding: 7px 44px;
  font-family: var(--accent-font);
  font-size: 16px;
  line-height: 20px;
  text-transform: uppercase;
  text-decoration: none;
  color: #ffffff;
  width: fit-content;
  background-color: #68b738;
  border: none;
}
.button--size_wide {
  width: 100%;
}

.catalog-page__title {
  margin: 21px 0 47px;
  font-size: 36px;
  line-height: 36px;
}

.promo__wrapper {
  color: #ffffff;
  background-image: url("../img/index-background-cat.jpg");
  background-repeat: no-repeat;
  background-position: center;
}

.promo__title {
  margin: 0;
  padding: 27px 0 25px;
  font-size: 36px;
  line-height: 36px;
  font-weight: 400;
}

.promo__subtitle {
  margin: 0;
  padding-bottom: 164px;
  font-size: 14px;
  line-height: 14px;
  text-transform: uppercase;
  font-weight: 400;
}

.promo__button {
  margin: 0 auto;
  font-size: 14px;
  line-height: 18px;
}

.selection__wrapper {
  margin: 20px 0;
  text-align: center;
  background-color: #f2f2f2;
}

.selection__title {
  margin: 0;
  padding: 30px 0 29px;
  font-size: 24px;
  line-height: 37px;
  font-weight: 400;
  text-transform: uppercase;
}

.selection__description {
  margin: 0 33px 0 20px;
  padding-bottom: 23px;
  font-size: 14px;
  line-height: 18px;
  text-align: left;
  color: #444444;
  border-bottom: 1px solid #d9d9d9;
}

.selection__link {
  display: block;
  padding: 19px 0 22px 19px;
  font-size: 16px;
  line-height: 16px;
  text-decoration: none;
  text-transform: uppercase;
  text-align: left;
  color: #000000;
}

.main-navigation__list {
  margin: 0;
  padding: 0;
  list-style-type: none;
}

.main-navigation__item:not(:nth-child(2n)) {
  border-top: 1px solid #e6e6e6;
  border-bottom: 1px solid #e6e6e6;
}

.main-navigation__link {
  display: block;
  padding: 22px 0 24px;
  font-size: 20px;
  line-height: 20px;
  font-weight: 500;
  text-align: center;
  text-transform: uppercase;
  color: #000000;
  text-decoration: none;
}

.advantages__title {
  margin-bottom: 40px;
  font-size: 36px;
  line-height: 40px;
  font-weight: 400;
}

.advantages__list {
  margin: 0;
  padding: 0;
  list-style-type: none;
}

.advantages__item {
  padding-bottom: 20px;
}

.advantages__item:last-child {
  padding-bottom: 49px;
}

.advantages__icon {
  margin-right: 20px;
  width: 60px;
  height: 60px;
  background-color: #68b738;
}

.advantages__description {
  margin: 0;
  font-size: 14px;
  line-height: 18px;
  color: #444444;
}

.example {
  background-color: #eaeaea;
}

.example__title {
  margin: 0;
  font-size: 36px;
  line-height: 40px;
  font-weight: 400;
}

.example__description {
  margin: 0;
  font-size: 14px;
  line-height: 18px;
  color: #444444;
}

.statistic__list {
  margin: 20px 0;
}

.statistic__key {
  font-size: 12px;
  line-height: 12px;
  text-transform: lowercase;
  color: #444444;
}

.statistic__value {
  margin: 0;
  font-size: 24px;
  line-height: 12px;
}

.statistic__list--spending .statistic__key {
  font-size: 14px;
  line-height: 20px;
  color: #000000;
  text-transform: uppercase;
}

.statistic__list--spending .statistic__value {
  font-size: 14px;
  line-height: 20px;
  text-transform: uppercase;
}

.form__list {
  margin: 0;
  padding: 0;
  list-style-type: none;
}

.form__item {
  margin-bottom: 14px;
}
.form__item:last-child {
  margin: 0;
  padding-bottom: 24px;
}

.form__label {
  font-size: 16px;
  line-height: 18px;
  text-transform: uppercase;
  color: #444444;
}

.form__input {
  padding: 13px 15px;
  font-family: inherit;
  color: #444444;
  border: 1px solid #e7e7e7;
}

.information__label {
  font-size: 16px;
  line-height: 18px;
  text-transform: uppercase;
  color: #444444;
}

.program__list {
  margin: 0;
  padding: 22px 0 19px;
  list-style-type: none;
  border-top: 1px solid #d9d9d9;
  border-bottom: 1px solid #d9d9d9;
}

.program__item:not(:last-child) {
  margin-bottom: 13px;
}

.contacts__form-group {
  padding: 0;
}

.comments__texarea {
  border: 1px solid #e7e7e7;
}

.extras__form-group {
  margin: 0;
  padding: 0;
}

.extras__list {
  margin: 0;
  padding: 0;
  list-style-type: none;
}

.extras__item {
  margin-bottom: 13px;
}
.extras__item:last-child {
  margin-bottom: 34px;
}

.extras__control-label {
  color: #000000;
}

.form-group {
  border: none;
  border-top: 2px solid #68b738;
}

.form-group__title {
  margin: 26px 0 34px;
  padding: 0;
  font-size: 24px;
  line-height: 26px;
}

.send__button {
  margin-bottom: 21px;
}

.send__description {
  margin: 0;
  font-size: 14px;
  line-height: 16px;
  text-align: center;
}

.catalog__list {
  margin: 0;
  padding: 0;
  list-style-type: none;
}

.catalog__item {
  margin: 0;
  padding: 0;
}

.card-product__link {
  text-decoration: none;
  color: #000000;
}

.card-product__title {
  margin: 0;
  font-size: 16px;
  line-height: 20px;
  color: #222222;
}

.card-product__list {
  margin: 0;
  font-size: 12px;
  line-height: 16px;
  color: #444444;
}

.card-product__value {
  margin: 0;
}

.card-product__order-button--show-more-button {
  color: #444444;
  background-color: #f2f2f2;
}

.extra-product__title {
  margin: 25px 0 36px;
  font-size: 24px;
  line-height: 24px;
}

.product-more__list {
  margin: 0;
  padding: 0;
  list-style-type: none;
}

.product-more__item {
  padding: 0;
}

.product-more__title {
  margin: 0;
  font-size: 16px;
  line-height: 16px;
  color: #222222;
}

.product-more__description {
  font-size: 14px;
  line-height: 14px;
  color: #444444;
}

.gift {
  background-image: url("../img/cat-background-catalog.jpg");
  background-repeat: no-repeat;
}

.gift__img {
  padding: 59px 0 48px;
  width: 78px;
  height: 80px;
}

.gift__description {
  margin: 0 0 35px;
  padding: 0 0 43px;
}

.cooperation {
  line-height: 20px;
}

.cooperation__title {
  margin: 0;
  font-size: 16px;
  text-transform: uppercase;
}

.address {
  font-size: 14px;
  font-style: normal;
  color: #444444;
}

.social-networks {
  border-top: 1px solid #d9d9d9;
  border-bottom: 1px solid #d9d9d9;
}

.social-networks__list {
  margin: 0;
  padding: 0;
  list-style-type: none;
}